import React, { useState, useEffect, useRef, forwardRef, useImperativeHandle } from 'react';
import GridLayout from 'react-grid-layout';
import 'react-grid-layout/css/styles.css';
import 'react-resizable/css/styles.css';
import './MyGrid.css';

const MyGrid = forwardRef((props, ref) => {
  const gridContainerRef = useRef(null);
  const [layout, setLayout] = useState([]);
  const [counter, setCounter] = useState(0);
  const [locked, setLocked] = useState(false);
  const [gridWidth, setGridWidth] = useState(800);
  const [gridItems, setGridItems] = useState({});
  const [showModal, setShowModal] = useState(false);
  const [formData, setFormData] = useState({
    name: '',
    description: '',
    image: null,
  });

  useEffect(() => {
    if (gridContainerRef.current) {
      setGridWidth(gridContainerRef.current.offsetWidth);
    }

    const handleResize = () => {
      if (gridContainerRef.current) {
        setGridWidth(gridContainerRef.current.offsetWidth);
      }
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Function to handle adding grid item
  const addGridItem = () => {
    if (layout.length < 12 && formData.name && formData.image) {
      const uniqueId = `${Date.now()}`; // Use timestamp as a unique key

      const newItem = {
        i: uniqueId,
        x: (layout.length * 2) % 12,
        y: Math.floor(layout.length / 6),
        w: 2,
        h: 2,
      };

      // Show image instantly using local URL
      const imageURL = URL.createObjectURL(formData.image); // Create local URL for immediate feedback
      setLayout((prevLayout) => [...prevLayout, newItem]);
      setGridItems((prevItems) => ({
        ...prevItems,
        [uniqueId]: {
          name: formData.name,
          description: formData.description,
          image: imageURL, // Local image shown immediately
        },
      }));

      // Start background upload to backend
      uploadImageToBackend(formData.image);

      // Reset form and close modal
      setFormData({ name: '', description: '', image: null });
      setShowModal(false);
    }
  };


  // Function to upload image to backend
  const uploadImageToBackend = async (imageFile) => {
    const formDataToSend = new FormData();
    formDataToSend.append('image', imageFile);

    try {
      const response = await fetch(`${window.location.origin}/api/grid/upload`, {
        method: 'POST',
        body: formDataToSend,
      });

      const result = await response.json();
      if (result.success) {
        console.log('Image uploaded successfully:', result.data.url);
      } else {
        console.error('Upload failed:', result.error);
      }
    } catch (error) {
      console.error('Error uploading image to backend:', error);
    }
  };


  const handleAddGridItem = () => {
    setShowModal(true);
  };

  const popGridItem = () => {
    if (layout.length > 0) {
      const newLayout = layout.slice(0, -1);
      setLayout(newLayout);

      const updatedGridItems = { ...gridItems };
      delete updatedGridItems[`${layout.length - 1}`];
      setGridItems(updatedGridItems);

      setCounter((prevCounter) => prevCounter - 1);
    }
  };

  const toggleLock = () => {
    setLocked(!locked);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setFormData((prevData) => ({
        ...prevData,
        image: file,
      }));
    }
  };

  useImperativeHandle(ref, () => ({
    addGridItem: handleAddGridItem,
    removeGridItem: popGridItem,
    toggleLock,
    isLocked: locked,
  }));

  const handleRemoveItem = (itemId) => {
    setLayout((prevLayout) => prevLayout.filter((item) => item.i !== itemId));
    setGridItems((prevItems) => {
      const updatedItems = { ...prevItems };
      delete updatedItems[itemId];
      return updatedItems;
    });
  };

  return (
    <div ref={gridContainerRef} className="grid-container">
      <GridLayout
        className="layout"
        layout={layout}
        cols={12}
        rowHeight={30}
        width={gridWidth}
        isDraggable={!locked}
        isResizable={!locked}
        margin={[10, 10]}
        containerPadding={[0, 0]}
        onLayoutChange={(newLayout) => setLayout(newLayout)}
      >
        {layout.map((item) => (
          <div key={item.i} className="grid-item">
            <div className="grid-item-content">
              {gridItems[item.i]?.image && (
                <img
                  src={gridItems[item.i].image}
                  alt={`Uploaded for item ${item.i}`}
                  className="uploaded-image"
                />
              )}
              <button
                className="cancel-button"
                onClick={() => handleRemoveItem(item.i)}
              >
                &times;
              </button>
            </div>
          </div>
        ))}
      </GridLayout>

      {showModal && (
        <div className="modal-overlay">
          <div className="modal-content">
            <h2>Add New Item</h2>
            <form>
              <input
                type="text"
                name="name"
                placeholder="Name"
                value={formData.name}
                onChange={handleInputChange}
              />
              <input
                type="text"
                name="description"
                placeholder="Description"
                value={formData.description}
                onChange={handleInputChange}
              />
              <input
                type="file"
                name="image"
                accept="image/*"
                onChange={handleImageChange}
              />
              <div className="modal-buttons">
                <button type="button" onClick={addGridItem} className="button">
                  Add
                </button>
                <button
                  type="button"
                  onClick={() => setShowModal(false)}
                  className="button"
                >
                  Cancel
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
});

export default MyGrid;
